.task {
    transition: all 1s linear; }
.anim-enter {
  opacity: 0.01;
  transform: scale(0.1); }

.anim-enter.anim-enter-active {
  opacity: 2;
  transition: all 0.5s ease-out;
  transform: scale(1); }
.anim-appear.anim-appear-active {
  opacity: 2;
  transition: all 0.5s ease-out;
  transform: scale(1); }

.anim-leave.anim-leave-active {
  opacity: 0.1;
  transition: all 0.5s ease-out;
  transform: scale(0.1); }
