$task-color: #323339;
.add-task {
    background-color: $task-color;
    margin-top: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 5px;
    color: white; }
.add-task__name {
    width: 100%;
    min-height: 180px;
    resize: none;
    overflow: visible; }

.number-fields {
    margin-top: 25px; }

.add-task__name-label {
    font-size: 2rem;
    width: 100%; }

.add-task__number-label {
    text-align: center; }

.add-task__color {
    display: none; }

.color-radio:hover {
    cursor: pointer;
    box-shadow: 0 0 5px white;
    transition: 0.2s; }

.color-radio {
    height: 50px;
    width: 50px;
    display: block;
    border-radius: 50%;
    margin: 0 5px;
    border: 2px solid white; }

.add-task__color:checked +.color-radio {
    border: 4px solid #6CBF84; }
