.task-container {
  border-radius: 5px; }

.task-container .col-md-6 {
  padding: 0; }

.task-container .col-md-6:nth-child(even) {
  padding-left: 10px; }

@media screen and (max-width:768px) {
  .task-container .col-md-6:nth-child(even) {
    padding-left: 0px; } }
h2 {
  display: block !important;
  word-break: break-all; }

